import TypeIt from "typeit-react";

const Header = () => {
  return (
    <div className="header">
      <div className="intro">
        <TypeIt
          options={{
            strings: [
              ".",
              "  ",
              " ",
              " .",
            ],
            speed: 50,
            startDelay: 1000,
            breakLines: true,
          }}
        />
      </div>
    </div>
  );
};
export default Header;
